<template>
  <!-- eslint-disable max-len -->
  <!-- eslint-disable vuejs-accessibility/label-has-for -->
  <main class='content container'>
    <div class='content__top'>
      <ul class='breadcrumbs'>
        <li class='breadcrumbs__item'>
          <a class='breadcrumbs__link' href='index.html'>
            Каталог
          </a>
        </li>
        <li class='breadcrumbs__item'>
          <a class='breadcrumbs__link' href='cart.html'>
            Корзина
          </a>
        </li>
        <li class='breadcrumbs__item'>
          <a class='breadcrumbs__link'>
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class='content__title'>
        Корзина
      </h1>
      <span class='content__info'>
        {{ products.length }} товара
      </span>
    </div>
    <div class='windows8' v-if='formLoading'>
      <div class='wBall' id='wBall_1'>
        <div class='wInnerBall'></div>
      </div>
      <div class='wBall' id='wBall_2'>
        <div class='wInnerBall'></div>
      </div>
      <div class='wBall' id='wBall_3'>
        <div class='wInnerBall'></div>
      </div>
      <div class='wBall' id='wBall_4'>
        <div class='wInnerBall'></div>
      </div>
      <div class='wBall' id='wBall_5'>
        <div class='wInnerBall'></div>
      </div>
    </div>

    <section class='cart'>
      <form class='cart__form form' action='#' method='POST' @submit.prevent='order'>
        <div class='cart__field'>
          <div class='cart__data'>
            <BaseFormText v-model='formData.name' :error='formError.name' title='ФИО'
                          placeholder='Введите ваше полное имя' />
            <BaseFormText v-model='formData.address' :error='formError.address'
                          title='Адрес доставки'
                          placeholder='Введите ваш адрес' />
            <BaseFormText v-model='formData.phone' :error='formError.phone'
                          title='Телефон'
                          placeholder='Введите ваш телефон' />
            <BaseFormText v-model='formData.email' :error='formError.email'
                          title='Email'
                          placeholder='Введите ваш Email' />
            <BaseFormTextarea title='Комментарий к заказу' v-model='formData.comment'
                              :error='formError.comment' placeholder='Ваши пожелания' />
          </div>

          <div class='cart__options'>
            <h3 class='cart__title'>Доставка</h3>
            <ul class='cart__options options'>
              <li class='options__item'>
                <label for='delivery' class='options__label'>
                  <input class='options__radio sr-only' type='radio' name='delivery' value='0'
                         checked=''>
                  <span class='options__value'>
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class='options__item'>
                <label for='delivery' class='options__label'>
                  <input class='options__radio sr-only' type='radio' name='delivery' value='500'>
                  <span class='options__value'>
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class='cart__title'>Оплата</h3>
            <ul class='cart__options options'>
              <li class='options__item'>
                <label for='pay' class='options__label'>
                  <input class='options__radio sr-only' type='radio' name='pay' value='card'>
                  <span class='options__value'>
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class='options__item'>
                <label for='pay' class='options__label'>
                  <input class='options__radio sr-only' type='radio' name='pay' value='cash'>
                  <span class='options__value'>
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class='cart__block'>
          <ul class='cart__orders'>
            <li class='cart__order' v-for='item in products' :key='item.id'>
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numberFormat }}₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class='cart__total'>
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму
              <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class='cart__button button button--primery' type='submit'>
            Оформить заказ
          </button>
        </div>
        <div class='cart__error form__error-block' v-if='formErrorMessage'>
          <h4>{{ formErrorMessage }}}</h4>
          <p>
            Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  filters: { numberFormat },
  components: {
    BaseFormText,
    BaseFormTextarea,
  },
  data() {
    return {
      formLoading: false,
      formData: {},
      formError: {},
      formErrorMessage: '',
    };
  },
  methods: {
    order() {
      this.formLoading = true;
      this.formError = {};
      this.formErrorMessage = '';
      clearTimeout(this.orderTimer);
      this.orderTimer = setTimeout(() => {
        axios
          /* eslint-disable */
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then(response => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({
              name: 'orderInfo',
              params: { id: response.data.id },
            });
          })
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => this.formLoading = false);
      }, 0);

    },
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
  },
};
</script>
