/* eslint-disable no-shadow */
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],
    productsLoading: false,
    productsLoadingFailed: false,

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, {
      productId,
      amount,
    }) {
      const item = state.cartProducts.find((item) => item.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      /* eslint-disable */
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      /* eslint-disable */
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        };
      });
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.product.price * item.amount + acc,
        // eslint-disable-next-line comma-dangle
        0,
      );
    }
    ,
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios
        /* eslint-disable */
        .get(API_BASE_URL + '/api/orders/' + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then(response => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      this.state.productsLoading = true;
      this.state.productsLoadingFailed = false;
      clearTimeout(this.cartProductsLoadingTimer);
      this.cartProductsLoadingTimer = setTimeout(() => {
        return axios
          /* eslint-disable */
          .get(API_BASE_URL + '/api/baskets', {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then(response => {
            this.productData = response.data;
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              context.commit('updateUserAccessKey', response.data.user.accessKey);
            }

            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
          .catch(() => this.state.productsLoadingFailed = true)
          .then(() => this.state.productsLoading = false);
      }, 0);

    },
    addProductToCart(context, {
      productId,
      amount,
    }) {
      return (new Promise(resolve => setTimeout(resolve, 2000)))
        .then(() => {
          return axios.post(API_BASE_URL + '/api/baskets/products', {
            productId: productId,
            quantity: amount,
          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
            .then(response => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            });
        });
    },
    updateCartProductAmount(context, {
      productId,
      amount,
    }) {
      context.commit('updateCartProductAmount', {
        productId,
        amount,
      });

      if (amount < 1) {
        return;
      }

      return axios.put(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);

        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

    deleteCartProduct(context, productId) {

      return axios.delete(API_BASE_URL + '/api/baskets/products', {
        data: {
          productId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },

      })
        .then(response => {

          context.commit('deleteCartProduct', productId);
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });

    },

  },

})
;
